.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 42px 0 0;
  max-width: 880px;
  margin: 0 auto;
  position: relative;
}

@media screen and (min-width: 1024px) {
  .profile {
    flex-direction: row;
    padding: 40px 0 0;
    column-gap: 30px;
  }
}